body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  background-color: #360863;
  min-height: 100vh;
  display: flex;
  justify-content: space-evenly;
  background-image: url(/casino-app/static/media/bg.84e1352a.png);
  background-position: 450%;
  background-size: 84%;
  background-repeat: no-repeat;
  font-size: calc(10px + 2vmin);
  color: white; }
  .App .App-content {
    width: 90%;
    display: flex; }
    .App .App-content .App-content-info {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      align-items: flex-start; }
      .App .App-content .App-content-info .App-content-info-paragraph {
        color: #f8f1ffb8; }
      .App .App-content .App-content-info .App-content-info-mobile {
        display: none; }
    .App .App-content .App-content-form {
      margin-top: 5%; }
    .App .App-content .App-content-center {
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end; }
      .App .App-content .App-content-center img {
        width: calc(100% - 20px);
        height: 80%; }

@media screen and (max-width: 800px) {
  .App {
    background-position: -20%;
    background-size: 360%; }
    .App .App-content {
      flex-direction: column; }
      .App .App-content .App-content-info {
        margin-top: 5%; }
        .App .App-content .App-content-info .App-content-info-paragraph {
          display: none; }
        .App .App-content .App-content-info .App-content-info-mobile {
          color: #f8f1ffb8;
          display: block; }
      .App .App-content .App-content-center {
        display: none; }
    .App .App-content-form {
      margin-top: unset; } }

.crypto-priceList-container {
  display: flex;
  flex-direction: column;
  width: 90%; }
  .crypto-priceList-container hr {
    width: 100%;
    border: 0.5px solid #f9f9f91c; }
  .crypto-priceList-container .crypto-priceList-container-item {
    display: flex;
    justify-content: space-between; }
    .crypto-priceList-container .crypto-priceList-container-item .crypto-priceList-container-item-col {
      font-size: calc(4px + 2vmin);
      width: 60%;
      line-height: 0.8em; }
    .crypto-priceList-container .crypto-priceList-container-item .crypto-priceList-container-item-close {
      vertical-align: middle;
      color: #f8f1ffb8 !important;
      height: auto; }

@media screen and (max-width: 800px) {
  .crypto-priceList-container {
    margin: 5%; }
    .crypto-priceList-container .crypto-priceList-container-item {
      margin-top: 10%; } }

